import './sass/main.scss';

const formSerialize = formElement => {
  const values = {};
  const inputs = formElement.elements;

  for (let i = 0; i < inputs.length; i++) {
    values[inputs[i].name] = inputs[i].value;
  }
  return values;
}

const dumpValues = form => () => { 
  const r = formSerialize(form);
  console.log(r);
  console.log(JSON.stringify(r));
  let data = "text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(r));
  return data;  
}

window.retrieveDMJson = () => {
    let f = document.querySelector("#dm");
    let data = dumpValues(f)();
    let a = document.querySelector('.icon');

    //hacks
    let temp = document.createElement('a');
    temp.className = 'icon'
    temp.href = 'data:' + data;
    temp.download = 'data.json';
    temp.innerHTML = '<i class="fa fa-download" aria-hidden="true"></i>';
    temp.target = "_blank";

    a.parentNode.replaceChild(temp, a);
    temp.click(); 

    temp.parentNode.replaceChild(a, temp);
}

const uploader = document.querySelector("#json");
    uploader.addEventListener("change",  (event) => {
    let reader = new FileReader();
    reader.readAsText(event.target.files[0]);

    reader.onload = (ev) => {
        let obj = JSON.parse(ev.target.result);
        console.log(obj);
        for( let prop in obj ){
            // console.log( prop, obj[prop] );
            let ctx = document.querySelector('input[name=' + prop +']');
            ctx.value = obj[prop];
        }
    }
})



const prepareSchedule = () => {

    const scheduleContentTemplate = (it) => `
     <div class="content schedule-${it}">
        <div class="big-input-first">
            <input type="text" name="schedule-${it}-opener">
        </div>  
        <div class="input-box">
            <input type="text" name="schedule-${it}-task-1">
            <input type="text" name="schedule-${it}-task-2">
        </div>
        <div class="last checker schedule-${it}-closer">      
        </div> 
     </div>
    `;
    let schedule = document.querySelector(".week__schedule");
    let it = 2;
    for(let i = 0; i < 10; i++) {
      schedule.innerHTML += scheduleContentTemplate(it++);
    }    
}

function main()
{
    prepareSchedule();

    //checker -> checkable
    const checkers = document.querySelectorAll(".checker");
    checkers.forEach(c => c.addEventListener('click', () => {
            c.classList.toggle('checked');
        })
    );

    const reflectionsCheckers = document.querySelectorAll(".fa-toggler");
    reflectionsCheckers.forEach(c => c.addEventListener('click', () => {
        let classes = c.classList;

        if(classes.contains("fa-posi")) {

            if (classes.contains("fa-check")) {
                classes.remove("fa-check");
                classes.add("fa-check-circle-o");   
            } else {                
                classes.add("fa-check");
                classes.remove("fa-check-circle-o");
            }

            let friend = c.closest(".reflection-kit").querySelector(".fa-neg");
             friend.classList.remove("fa-times-circle-o");
            friend.classList.add("fa-times");
        }

        if (classes.contains("fa-neg")) {

            if (classes.contains("fa-times")) {
                classes.remove("fa-times");
                classes.add("fa-times-circle-o");
            } else {
                classes.add("fa-times");
                classes.remove("fa-times-circle-o");
            }

            let friend = c.closest(".reflection-kit").querySelector(".fa-posi");
            
            friend.classList.remove("fa-check-circle-o");
            friend.classList.add("fa-check");
        }
    }));
}   

main();


